
import XHeader from 'vux/src/components/x-header/index.vue'
import PopupHeader from 'vux/src/components/popup-header/index.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Card from 'vux/src/components/card/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import FormPreview from 'vux/src/components/form-preview/index.vue'
import Divider from 'vux/src/components/divider/index.vue'

import {httpUrl} from '../../http_url';

export default {
directives: {
TransferDom
},
data () {
return {
menus: {
menu1: '退出登录'
},
phone:'',
showMenus: false,
showCancleOrder:false,
showInput:true,
cancleReason:'',
status_name:'',
totalprice:'',
product_data:'',
flight_price:'',
hotel_price:'',
other_price:'',
fenqi_price:'',
stage:'',
paymoney:'',
orderid:'',
sellername:'',
cxrInfo:[],
flightInfo:[],
hotelInfo:[],
status_type:'',
remark:'',
other_info:'',
pdfurls:'',
pdfTime:''

}
},
created(){
this.getData();
},
components: {
XHeader,
PopupHeader,
Actionsheet,
Card,
Tab, TabItem,XButton,XTextarea,FormPreview,Divider
},
computed: {
amountPrice: function () {
let sum = parseFloat(this.totalprice) + parseFloat(this.fenqi_price);
return sum.toFixed(2);
},
},
methods:{
getData(){
let orderid = this.$route.query.orderid;
const _this = this;
let postData = this.$qs.stringify({
orderid:orderid
})
this.$axios({
method: 'post',
url:httpUrl.orderInfo,
data:postData
}).then((res)=>{
console.log(res.data)
let data =res.data.retData;
if(res.data.errCode==200){
this.status_name = data.orderInfo.status_name;
this.status_type = data.orderInfo.status_type;
this.product_data = data.orderInfo.product_data;
this.other_price = data.orderInfo.other_price;
this.flight_price = data.orderInfo.flight_price;
this.hotel_price = data.orderInfo.hotel_price;
this.fenqi_price = data.orderInfo.fenqi_price;
this.stage = data.orderInfo.stage;
this.paymoney = data.orderInfo.paymoney;
this.orderid = data.orderInfo.orderid;
this.sellername = data.orderInfo.sellername;
this.cxrInfo = data.people;
this.totalprice = data.orderInfo.totalprice;
this.hotelInfo = data.hotel;
this.flightInfo = data.flight;
this.other_info = data.orderInfo.other_info;
this.pdfurls = data.fuben.ht_url;
this.pdfTime = data.fuben.create_time;
this.remark = data.orderInfo.remark;
}else if(res.data.errCode==201){
this.$vux.toast.show({
type:'warn',
text: res.data.retMsg
})
}else if(res.data.errCode==401){
this.$vux.alert.show({
title: '错误提示',
content: '未登录，请先登录',
onHide () {
_this.$router.push('/quick_login');
}
})
}
}).catch(function () {
this.$vux.toast.show({
text: '网络异常，请稍后再试',
type:'warn'
})
});
},
openHt(){
window.open(this.pdfurls);
},
openVideoPlay(){
let orderid = this.$route.query.orderid;
this.$router.push({
path:'/video_play',
query: {
orderid:orderid,
billdetail:1           
}
});
},
goFenqiInfo(){
let orderid = this.$route.query.orderid;
this.$router.push({
path:'/fenqi_info',
query: {
orderid:orderid
}
});
},
cancleBtn(){
this.showCancleOrder =true;
},
CancleOrder(){
let _this = this;
let orderid = this.$route.query.orderid;
let postData = this.$qs.stringify({
orderid:orderid,
status:0,
remark:this.cancleReason
})
_this.$axios({
method: 'post',
url:httpUrl.cancelorder,
data:postData
}).then((res)=>{
console.log(res.data)
if(res.data.errCode==200){
_this.$vux.toast.show({
type:'success',
text: '订单取消成功',
onHide () {
_this.$router.push('/bill_list');
}
})
}else if(res.data.errCode==201){
_this.$vux.toast.show({
type:'warn',
text: res.data.retMsg
})
}else if(res.data.errCode==401){
_this.$vux.alert.show({
title: '错误提示',
content: '未登录，请先登录',
onHide () {
_this.$router.push('/quick_login');
}
})
}
}).catch(function () {
this.$vux.toast.show({
text: '网络异常，请稍后再试',
type:'warn'
})
});
},
submit(){
let orderid = this.$route.query.orderid;
this.$router.push({
path:'/ht_preview',
query: {
orderid:orderid
}
});
},

onCancel(){
this.$router.push('/bill_list');
},
getWxPayData(){
var vm = this;
let orderid = this.$route.query.orderid;
let postData = this.$qs.stringify({
orderid:orderid
})
this.$axios({
method: 'post',
url:'http://ybuser.youxia.com/Pay/wxPay',
headers: {
'Content-Type': 'application/x-www-form-urlencoded;charset=UTF-8'
},
data:postData
}).then((res)=>{
if(res.data.errCode==201){
this.$vux.toast.show({
type:'warn',
text: res.data.retMsg
})
}else {
vm.weixinPay(JSON.parse(res.data));
}
})
},
weixinPay(data){
var vm= this;
if (typeof WeixinJSBridge == "undefined"){//微信浏览器内置对象。参考微信官方文档
if( document.addEventListener ){
document.addEventListener('WeixinJSBridgeReady', vm.onBridgeReady(data), false);
}else if (document.attachEvent){
document.attachEvent('WeixinJSBridgeReady', vm.onBridgeReady(data));
document.attachEvent('onWeixinJSBridgeReady',vm.onBridgeReady(data));
}
}else{
vm.onBridgeReady(data);
}
},
onBridgeReady:function(data) {
var vm = this;
WeixinJSBridge.invoke(
'getBrandWCPayRequest', {
"appId": data.appId,     //公众号名称，由商户传入
"timeStamp": data.timeStamp, //时间戳，自1970年以来的秒数
"nonceStr": data.nonceStr, //随机串
"package": data.package,
"signType": data.signType, //微信签名方式：
"paySign": data.paySign //微信签名
},
function (res) {

if (res.err_msg == "get_brand_wcpay_request:ok") {
vm.$vux.toast.show({
text: '支付成功',
type:'success',
onHide () {
vm.$router.push('/home');
}
})
} else if (res.err_msg == "get_brand_wcpay_request:fail") {
vm.$vux.toast.show({
text: '支付失败' + res.err_msg,
type:'warn',
onHide () {
vm.$router.push('/home');
}
})
}
}
);
},
LoginOut(){
this.$axios({
method: 'post',
url:httpUrl.loginOut,
headers: {
'Content-Type': 'application/x-www-form-urlencoded;charset=UTF-8'
},
}).then((res)=>{
console.log(res.data)
if(res.data.errCode==200){
this.$router.push('/quick_login');
}else if(res.data.errCode==201){
this.$vux.toast.show({
type:'warn',
text: res.data.retMsg
})
}

}).catch(function () {
this.$vux.toast.show({
text: '网络异常，请稍后再试',
type:'warn'
})
});
},
goHome(){
this.$router.push('/home');
},
goCreditInfoList(){
this.$router.push('/credit_info_list');
}
}
}



